.App {
  margin: auto;
  text-align: center;
  max-width: 900px;
  height: 100%;
  min-height: 100vh;
  background-color: rgba(222, 184, 135, 0.6);
}

.header {
  width: 100%;
  height: auto;
}

form {
  background-color: aquamarine;
  margin: auto;
  height: 200px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

input {
  margin: 1%;
}

.note-display{
  padding: 1%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: auto;
  grid-gap: 2%;
}

.note {
  background-color: beige;
  margin: 2%;
  padding: 1%;
  display: grid;
  grid-template-rows: 30% 70%;
  height: 15rem;
  box-shadow: 2px 5px 9px 1px rgba(0,0,0,0.71)
  ;
}

h1 {
  margin: 0%;
}

.note-header {
  margin: 3%;
  grid-area: 1 / 1 / 2 / 2;
  display: flex;
 justify-content: space-around;
}

.add-note-title {
  margin-top: 10%;
  text-align: center;
  color: rgba(127, 255, 212, 0.6);
}

button {
  background-color: burlywood;
  border: none;
  cursor: pointer;
  padding: 2%;
  transition: 200ms;
}

button:hover {
  transform: scale(1.07);
  transition: 200ms;
}

img {
  height: 50px;
}

@media screen and (max-width: 600px) {
  .note-display{
    grid-template-columns: auto;
  }
}